    transition: transform 0.5s ease-in-out;
}

.rotate-90:hover {
    transform: rotate(90deg);
}

.rotate-360 {
    transition: transform 1s ease-in-out;
}

.rotate-360:hover {
    transform: rotate(360deg);
}

.shake {
    animation: shake 0.667s infinite;
}

